.footer {
	margin: 100px 0;
}
.contacts__form {
	padding: 75px 0;
}
.form {
	display: flex;
	gap: 35px;
}
.form__label {
	display: block;
	padding-top: 25px;
}
.input-tel {
	color: #fff;
	background-color: transparent;
	font-size: 50px;
	font-weight: 400;
	font-family: "Kharkiv";
}

.control-wrapper {
	flex: 1 1;
	border-bottom: solid #fff 2px;
}
.contacts__button {
	padding: 40px 55px;
	color: #fff;
	background-color: transparent;

	border: solid #fff 2px;
	background-color: transparent;
	font-size: 14px;
	text-transform: uppercase;
	transition: 0.3s ease border;
	&:hover {
		border: $red-accent 2px solid;
	}
}

.contacts__contact {
	display: flex;
	justify-content: space-between;
}

.contacts__descr {
	flex: 0 0 410px;

	color: #cbcbcb;
	font-size: 20px;
	line-height: 1.55;
}

.contacts__seti {
	display: flex;
	align-items: flex-end;
	gap: 60px;

	& a {
		border-bottom: solid $red-accent 1px;
		color: #cbcbcb;
		font-size: 20px;
		text-transform: uppercase;
	}
}
